
#root{
  background-color: #f0f0f0;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


/*------Cabecera-------*/

.Header{
  background-color: #000000;
  
  background-image: linear-gradient(270deg, rgba(255,255,255,1),  rgba(0,0,0,1),rgba(0,0,0,1));
}
#logo{
  margin:0;
  height: auto;
  width: 100px;
  display: block;
}
.menuHeader{
  margin:0;
  padding:0;
  display: block;
  list-style-type: none;
  background-color: #BFC8B2;
  width: 100%;
  float: right;
  
}

.noselected{
  display: inline;
  float: left;
  padding: 0.5% 2% 0.5% 2%;
  margin:0;
  font-weight: 400;
  color:#f0f0f0;
  background-color: #BFC8B2;
}
.noselected:hover{
  color:#333333;
  cursor:pointer;
}
.selected{
  display: inline;
  float: left;
  padding: 0.5% 2% 0.5% 2%;
  margin:0;
  font-weight: 400;  
  color:#333333;
}
.selected:hover{
  cursor:pointer;
}
/*------Body html------*/
body{
  background-color: #f0f0f0;
  margin-bottom: 10%;
}

/*------Tabla Explorar-------*/
thead{
  text-align: left;
  
}
thead th{
  text-align: center;
  border-bottom: 2px solid #000;
  font-family: 'Roboto';
}
th{
  margin: 0 8% 0 8%;
  font-family: 'Roboto';
  width: auto;
}
.table{
  width: 75%;
  margin-left: auto;
  margin-right: auto;

}


/*------Formulario Nueva Reparacion-------*/
#formNew{
  width: 750px;
  margin-left: auto;
  margin-right: auto;
}
#formEdit{
  width: 750px;
  margin-left: auto;
  margin-right: auto;
}
.form{
  display:grid;
  grid-template-columns:  repeat(6,auto);
  grid-template-rows: repeat(11,auto);
  justify-items: center;
  width: 750px;
  margin-left: auto;
  margin-right: auto;
 
}
h1{
  margin-top: 8%;
  grid-column: 1 / 7;
  grid-row:1;
  text-align: center;
  font-size: 40px;
  color:#333333;
}

#client{
  grid-column: 2 / 6;
  grid-row: 3;
  text-align: center;
  font-size: 30px;
  color:#333333;
}
.name{
  grid-column: 3;
  grid-row: 4;
  text-align: left;
}
.phone{
  grid-column: 4;
  grid-row: 4;
  text-align: left;
}

.equipment{
  text-align: center;
  grid-column: 2 /6;
  grid-row: 5;
  font-size: 30px;
  color:#333333;
}
.type{
  text-align: left;
  grid-column:  3;
  grid-row: 6;
}
.manufacturer{
  text-align: left;
  grid-column: 4;
  grid-row: 6;
}
.model{
  text-align: left;
  grid-column: 3;
  grid-row: 7;
}
.id{
    text-align: left;
    grid-column: 3;
    grid-row: 7;
}
.serial_number{
  text-align: left;
  grid-column: 4;
  grid-row: 7;
}
.backup{
  text-align: left;
  grid-column: 3;
  grid-row: 8;
}
.password{
  text-align: left;
  grid-column: 4;
  grid-row: 8;
}
.reception_date{
  text-align: left;
  grid-column: 3;
  grid-row: 9;
}
.estimated_cost{
  text-align: left;
  grid-column: 4;
  grid-row: 9;
}
.description{
  text-align: left;
  grid-column: 3 / 5;
  grid-row: 10;
}
.withdrawal_date{
  text-align: left;
  grid-column: 3 / 5;
  grid-row: 11;
}

label{
  display:block;
  font-family: 'Roboto';
}
.formBtn{
  text-align: center;
  grid-column: 3/5;
  grid-row: 12;
  border-radius: 5px;
  height: 55px;
  width: 100%;
  background-color: #fd802c;
  font-size: 20px;
  font-weight: 400;
  color:#333333;
  margin-top: 6%;
  border:none;
}
.formBtnHidden{
  text-align: center;
  grid-column: 3/5;
  grid-row: 12;
  display:none;
}
.formBtn:hover{
  background-color: #f8c29e;
  cursor:pointer;
}

.inputText{
  border: solid 1px #333333;
  border-radius: 5px;
  height: 35px;
  width: 300px;
  margin: 1% 0 1% 0;
}
textarea{
  height: 100px;
  width: 800px;
  border-radius: 5px;
  border: solid 1px #333333;
}
/*------repairment-----*/
.buttons{
  text-align: center;
  grid-column: 2/6;
  grid-row: 2;
  
}

/*------botones-------*/

.btn{
  list-style-type: none;
  padding: 5px 15px;
  border: none;
  border-radius: 5px;
  text-align: center;
  width: 100px;
  font-weight: 400;
  color: #333333;
}

#view{
  background-color: #54518a;
  display: block;
  margin: 1% 0 1% 5%;
}
#back{
  background-color: #54518a;
  display: inline;
  margin: 0 1% 0 1%;
}
#pdf{
  background-color: #42aa3f;
  display: inline;
  margin: 0 1% 0 1%;
}
#delete{
  background-color: #fd5b5b;
  display: inline;
  margin: 0 1% 0 1%;
}
#edit{
  background-color: #dd9b43;
  display: inline;
  margin: 0 1% 0 1%;
}
#view:hover{
  background-color: #7f7e8d;
  cursor:pointer;
}
#back:hover{
  background-color: #7f7e8d;
  cursor:pointer;
}
#pdf:hover{
  background-color: #bdf7bb;
  cursor:pointer;
}
#delete:hover{
  background-color: #fa9f9f;
  cursor:pointer;
}
#edit:hover{
  background-color: #fcc098;
  cursor:pointer;
}